<template>
    <div>
        <div>
            <div style="display: flex;justify-content: space-between">

                <div>
                    <el-button type="primary" icon="el-icon-plus" @click="showAddEmpView">
                        添加地图
                    </el-button>

                </div>
            </div>
            <el-table :data="maps" border stripe size="mini">
                <el-table-column type="selection" align="left" width="55"></el-table-column>
                <el-table-column prop="id" label="编号" fixed width="120" align="left"></el-table-column>
                <el-table-column prop="map_name" label="名称" width="120" align="left"></el-table-column>
                <el-table-column prop="view" label="预览" width="200" align="left">
               <template slot-scope="scope">
                <el-button @click="init(scope.row)" style="padding: 3px" size="mini">查看地图</el-button>
                </template>
                </el-table-column>
                <el-table-column prop="detail" label="详情" width="120" align="left"></el-table-column>
                <el-table-column prop="longitude" label="地图经度" width="120" align="left"></el-table-column>
                <el-table-column prop="altitude" label="地图维度" width="120" align="left"></el-table-column>
                <el-table-column label="操作" align="center">
                    <template slot-scope="scope">
                    <el-button @click="showEditEmpView(scope.row)" style="padding: 3px" size="mini">编辑</el-button>
                        <el-button  @click="deletemap(scope.row)" style="padding: 3px" size="mini">
                            删除地图
                        </el-button>
                    </template>da

                </el-table-column>

            </el-table>
            <div class="box" style=" display:flex,justify-content:flex-end">
                <div id="container" style="width:1500px; height:600px"></div>
            </div>
            <div style="display: flex;justify-content: flex-end,width:40%; height:40%">
                <el-pagination
                        background
                        @size-change="sizeChange"
                        @current-change="currentChange"
                        layout="sizes, prev, pager, next, jumper, ->, total, slot"
                        :total="total">
                </el-pagination>
            </div>


        </div>
            <el-dialog
                  :title="title"
                  :visible.sync="dialogVisible"
                  width="80%">
                    <div>
                        <el-form :model="emp" :rules="rules" ref="empForm">
                            <el-row>
                                <el-col :span="6">
                                    <el-form-item label="名称:" prop="map_name">
                                        <el-input size="mini" style="width: 150px" prefix-icon="el-icon-edit" v-model="emp.map_name"
                                                  placeholder="输入心得标题"></el-input>
                                    </el-form-item>
                                </el-col>

                            </el-row>
                            <el-row>
                                <el-col :span="6">
                                    <el-form-item label="经度:" prop="longitude">
                                       <el-input size="mini" style="width: 150px" prefix-icon="el-icon-edit" v-model="emp.longitude"
                                            placeholder="请输入地图经度"></el-input>
                                    </el-form-item>
                                </el-col>
                            </el-row>
                            <el-row>
                                <el-col :span="6">
                                    <el-form-item label="纬度:" prop="altitude">
                                       <el-input size="mini" style="width: 150px" prefix-icon="el-icon-edit" v-model="emp.altitude"
                                            placeholder="请输入地图纬度"></el-input>
                                    </el-form-item>
                                </el-col>
                            </el-row>
                            <el-row>
                                <el-col :span="6">
                                    <el-form-item label="详细信息:" prop="detail">
                                        <el-input size="mini" style="width: 150px" prefix-icon="el-icon-edit" v-model="emp.detail"
                                                  placeholder="请输入详细信息"></el-input>
                                    </el-form-item>
                                </el-col>
                            </el-row>

                        </el-form>
                    </div>
                    <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="doAddEmp">确 定</el-button>
          </span>
          </el-dialog>
    </div>
</template>

<script>
    export default {
        name: "Map",
        data() {
            return {
                maps: [],
                total: 0,
                title:'',
                dialogVisible:false,
                currentPage: 1,
                currentSize: 10,
                currentSalary: null,
                emp:{
                    name:"北京五道口",
                    altitude:"",
                    longitude:"",
                    detail:"宇宙中心"
                }
            }
        },
        mounted() {
            this.initEmps();
        },
        methods: {
            showEditEmpView(data){
                this.title = '编辑心得信息';
                this.emp = data;
                this.dialogVisible = true;
            },
            init (data) {
                /*let map = new AMap.Map('container', {
                    resizeEnable: true,
                    rotateEnable:true,
                    pitchEnable:true,
                    zoom: 17,
                    pitch:80,
                    rotation:-15,
                    viewMode:'3D',//开启3D视图,默认为关闭
                    buildingAnimation:true,//楼块出现是否带动画

                    expandZoomRange:true,
                    zooms:[3,20],
                    center:[data.longitude,data.altitude]
                })*/
                window.location.href ='http://localhost:8080/map.html?lat='+data.altitude+'&lon='+data.longitude+'&zoom=17.4&tilt=0.0&rotation=0.0'
                //this.$router.push({path: 'http://localhost:8080/map.html?lat='+data.latitude+'&lon='+data.longitude+'&zoom=17.4&tilt=0.0&rotation=0.0'})
            },
            deletemap(data){
                this.$confirm('此操作将永久删除【' + data.altitude + '】, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.deleteRequest("/map/" + data.id).then(resp => {
                        if (resp) {
                            this.initEmps();
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },

            doAddEmp() {
                if (this.emp.id) {
                    this.$refs['empForm'].validate(valid => {
                        if (valid) {
                            this.putRequest("/map/", this.emp).then(resp => {
                                if (resp) {
                                    this.dialogVisible = false;
                                    this.initEmps();
                                }
                            })
                        }
                    });
                } else {
                    this.$refs['empForm'].validate(valid => {
                        if (valid) {
                            this.postRequest("/map/", this.emp).then(resp => {
                                if (resp) {
                                    this.dialogVisible = false;
                                    this.initEmps();
                                }
                            })
                        }
                    });
                }
            },

            sizeChange(size) {
                this.currentSize = size;
                this.initEmps();
            },
            currentChange(page) {
                this.currentPage = page;
                this.initEmps();
            },

            showAddEmpView() {
                this.emptyEmp();
                this.title = '添加地图';
                this.getMaxWordID();
                this.dialogVisible = true;
            },
            getMaxWordID() {
                this.getRequest("/map/maxWorkID").then(resp => {
                    if (resp) {
                        this.emp.workID = resp.obj;
                    }
                })
            },
            emptyEmp(){
            this.emp={
            id:"",
            name:"",
            langitude:"",
            altitude:"",
            detail:"",

              }
            },
            showPop(data) {
                if (data) {
                    this.currentSalary = data.id;
                } else {
                    this.currentSalary = null;
                }
            },

            initEmps() {
                this.getRequest("/map/?page=" + this.currentPage + '&size=' + this.currentSize).then(resp => {
                    if (resp) {
                        this.maps = resp.data;
                        this.total = resp.total;
                    }
                })
            }
        }
    }
</script>
<style scoped>
.map-container {
  position: relative;
  left: 0;
  top: 0;
  width: 10%;
  height: 10%;
}
</style>
